table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  max-width: 100%;
  overflow-x: auto;
}

th, td {
  border: 1px solid black;
  text-align: center;
  padding: 8px;
  min-width: 150px;
  word-wrap: break-word;
}

th {
  background-color: #f2f2f2;
}

@media screen and (max-width: 768px) {
  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  th, td {
    padding: 5px;
    font-size: 12px;
    min-width: 100px;
  }
}